@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
.reg_body {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #514c51;
  position: relative;
}
.reg_body::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  background: #282429;
  clip-path: polygon(86% 0, 100% 0, 100% 100%, 60% 100%);
}
.reg_container {
  z-index: 12;
  max-width: 1050px;
  width: 100%;
  background: #000;
  border-radius: 12px;
  margin: 0 20px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
}
.reg_container .reg_content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 25px 20px;
}
.reg_content .image-box {
  max-width: 55%;
}
.reg_content .image-box img {
  width: 100%;
}
.reg_content .topic {
  font-size: 22px;
  font-weight: 500;
  color: white;
}
.reg_content form {
  width: 40%;
  margin-right: 30px;
}
.reg_content .input-box {
  height: 50px;
  width: 100%;
  margin: 16px 0;
  position: relative;
}
.reg_content .input-box .reg_input {
  /* position: absolute; */
  height: 100%;
  width: 100%;
  border-radius: 6px;
  font-size: 16px;
  outline: none;
  padding: 0 16px;
  background: #2a3133;
  color: white;
  border: 2px solid transparent;
  transition: all 0.3s ease;
}
.reg_content .input-box .reg_input:focus,
.reg_content .input-box .reg_input:valid {
  border-color: black;
  background-color: #2a3133;
}
.reg_content .input-box label {
  position: absolute;
  left: 18px;
  top: 50%;
  color: #636c72;
  font-size: 15px;
  pointer-events: none;
  transform: translateY(-50%);
  transition: all 0.3s ease;
}
.reg_content .input-box .reg_input:focus ~ label,
.reg_content .input-box .reg_input:valid ~ label {
  top: 0;
  left: 12px;
  display: 14px;
  color: white;
  background: rgba(0, 0, 0, 0);
}
.reg_content .message-box {
  min-height: 100px;
  position: relative;
}
