.reg_content .message-box textarea:focus {
  border-color: #ba24c2;
  background-color: #fff;
}
.reg_content .message-box label {
  position: absolute;
  font-size: 16px;
  color: #636c72;
  left: 18px;
  top: 6px;
  pointer-events: none;
  transition: all 0.3s ease;
}
.reg_content .message-box textarea:focus ~ label {
  left: 12px;
  top: -10px;
  color: #ba24c2;
  font-size: 14px;
  background: #fff;
}
.reg_content .input-box .reg_input[type="submit"] {
  color: #fff;
  background: #ba24c2;
  padding-left: 0;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  letter-spacing: 1px;
  transition: all 0.3s ease;
}
.reg_content .input-box .reg_input[type="submit"]:hover {
  background-color: #d43ddb;
}
@media (max-width: 1000px) {
  .reg_content .image-box {
    max-width: 70%;
  }
  .reg_content {
    padding: 10px 0;
  }
}
@media (max-width: 900px) {
  .reg_content .image-box {
    display: none;
  }
  .reg_content form {
    width: 100%;
    margin-left: 30px;
  }
}
