*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.APP{
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
footer{
  margin-top: auto;
}
body{
    position: relative;
    height: 100vh;
    width: 100%;
    background: rgb(226, 226, 233);
}

body.dark-mode{
    background: rgb(27, 27, 34);
}
.nav_login_link{
  text-decoration: none;
}
.nav_login{
  font-size: 14px;
  font-weight: 300;
  width: 16%;
  min-width: 66px;
max-width: 107px;
  position: absolute;
right: 8px;
border: rgb(0, 0, 0) solid 0.5px;
border-radius: 17px;
height: 31px;
display: flex;
align-items: center;
justify-content: center;
text-decoration: none;
}
.nav_login_logouser{
  cursor: pointer;
  min-width: 28px;
max-width: 40px;
  position: absolute;
right: 20px;
border: rgb(255, 255, 255) solid 0.5px;
border-radius: 50px;
width: 6vw;
display: flex;
align-items: center;
justify-content: center;
text-decoration: none;
transition: 0.2s ease-in-out;
}
.nav_login_logouser:hover{
  border: solid 3px white;
}
.sub-manue-wrap{
  background: rgba( 255, 255, 255, 0.8 );
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 5px );
-webkit-backdrop-filter: blur( 5px );
border-radius: 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
  position: absolute;
  top: 100%;
  right: 4%;
  width: 320px;
  max-height: 0px;
  overflow: hidden;
  transition:  0.5s ease-in-out;
  z-index: -1;
  opacity: 0;
}
.open{
  opacity: 1;
  max-height: 400px;
  z-index: 100;
}
.sub-manue-wrap .sab-manue{
 
padding: 20px;
margin: 10px;
}
.user-info{
  display: flex;
  align-items: center;
}
.user-info h4{ 
  color: black;
  font-weight: 300;
}
.user-info img{
  cursor: pointer;
  width: 40px;
  margin-right: 18px;
}
.user-info img:hover{
  border: solid white 3px;
}

.sab-manue hr{
  border: 0;
  height: 1px;
  background-color: #181616;
  margin: 15px 0px 10px;
}
.sub-manue-link{
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #525252;
  margin: 12px 0px;
  justify-content: center;
}
.sub-manue-link p{
  margin-top: 1rem !important;
  width: 100%;
  font-size: 20px;
  transition: transform 0.5s;
  color: black;
}
.sub-manue-link span{
  font-size: 20px;
  transition: transform 0.5s;
}
.sub-manue-link:hover span{
  transform: translateX(8px);
}
.sub-manue-link:hover p{
font-weight: 600;
}


nav{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-start; /* Modified from align-items: start; */
  position: fixed;
  width:235px;
  height: 100%;
  min-height: 350px;
  color: white;
  background-color: #151415;
  background: rgba(0, 0, 0, 0.801);
  border-top-right-radius: 20px;
  border-bottom-right-radius: 10px;
  transition: ease-in 0.3s;
  z-index: 100;
}
.nav_profilr{
  display: inline-flex;
  align-items: center;
  padding: 5px 15px;
  color: white;
  text-decoration: none;
  font-size: 25px;
  transition: ease-in 0.2s;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
}
