nav.hide{
    width: 0;
}


nav span{
    width: 100%;
    text-align: center;
    padding: 20px 0;
    font-size: 28px;
    font-weight: 800;
    overflow: hidden;
}

.sidebar-menu{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-around;
    height: 70%;
    width: 100%;
    list-style: none;
    overflow: hidden;
    padding-left: 0px
}

.sidebar-menu li{
    text-align: center;
    width: 100%;
}

.sidebar-menu li a{
    display: inline-flex;
    align-items: center;
    width: 95%;
    padding: 5px 15px;
    color: white;
    text-decoration: none;
    font-size: 25px;
    transition: ease-in 0.2s;
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 5px;
}

.sidebar-menu li a:hover{
    color: #ff00ff;
    border: 1px solid white;
}

.sidebar-menu li a i{
    margin: 0 18px;
    width: 20px;
}

#toggle-btn{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 13px;
    right: -39px;
    width: 40px;
    height: 40px;
    font-size: 18px;
    background: rgba(0, 0, 0, 0.801);
    border-radius: 0 7px 7px 0;
    border: none;
    cursor: pointer;
    color: white;
    transition: linear 0.2s;
}

#toggle-btn:hover{
    background: rgba(0, 0, 0, 0.623);
    color: #ff00ff;
}

section{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    position: absolute;
    top:0;
    left: 270px;
    height: 100vh;
    width: calc(100% - 270px);
    padding: 30px;
    transition: ease-in 0.3s;
}

section.expand{
    width: 100%;
    left: 0;
}

section h1{
    color: rgb(0, 0, 0);
    font-size: 32px;
    text-align: center;
}

section h1.dark-mode{
    color: white;
}
.navbar{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 62px;
    background:#141414;
}


  
