.skelton{
  animation: skelton-loading 1s linear infinite alternate;
}
@keyframes skelton-loading {
  0%{
    background-color: rgb(65, 65, 65);
  }
  100%{
    background-color: rgb(153, 153, 153);
  }
}
.search-dropdown{
  z-index: 999;
  height: 0;
  overflow: hidden;
  opacity: 0;
  width: 100%;
  background: rgba( 255, 255, 255, 0.6 );
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 4.5px );
-webkit-backdrop-filter: blur( 4.5px );
border-radius: 5px;
border: 1px solid rgba( 255, 255, 255, 0.18 );

}
.search-dropdown_active{

  z-index: 99;
 height: auto;
 max-height: 250px;
  overflow-y: auto;
  opacity: 1;
  width: 100%;
  background: rgba( 255, 255, 255, 0.6 );
box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 4.5px );
-webkit-backdrop-filter: blur( 4.5px );
border-radius: 0px 0px 10px 10px;
border: 1px solid rgba( 255, 255, 255, 0.18 );
}
.search-box{
 
  border-radius:13px ;
  border-bottom: solid rgb(255, 0, 255) 2px;
  border-top: none;
  border-left: none;
  border-right: none;
  height: 30px;
 width: 100%;
 padding-left: 5px;
 outline: none;
 color: white;
 caret-color: white;
 background-color: #141414;
}
