 .cart_cont{position: absolute;
  z-index: 20;
  left: 9px;
  top: -4px;
  color: white;
  font-size: 18px;
  font-weight: 700;}
  .btn_cart{
    color: rgb(36, 198, 115);
position: relative;
left: 30px;
top: -11px;
  }
  .cart_cont_in{
    position: absolute;
    z-index: 20;
    left: 10px;
    top: -6px;
    color: white;
    font-size: 20px;
    font-weight: 700;}

    .th_line{
      position: absolute;
    }


    .payment-info {
      background: #ff00f7;
      padding: 10px;
      border-radius: 6px;
      color: #fff;
      font-weight: bold;
    }
    
    .product-details {
      padding: 10px;
    }
    
    
    
    .cart {
      background: #fff;
    }
    
    .p-about {
      font-size: 12px;
    }
    
    .table-shadow {
      -webkit-box-shadow: 5px 5px 15px -2px rgba(0,0,0,0.42);
      box-shadow: 5px 5px 15px -2px rgba(0,0,0,0.42);
    }
    
    .type {
      font-weight: 400;
      font-size: 10px;
    }
    
    label.radio {
      cursor: pointer;
    }
    
    label.radio input {
      position: absolute;
      top: 0;
      left: 0;
      visibility: hidden;
      pointer-events: none;
    }
    
    label.radio span {
      padding: 1px 12px;
      border: 2px solid #ada9a9;
      display: inline-block;
      color: #8f37aa;
      border-radius: 3px;
      text-transform: uppercase;
      font-size: 11px;
      font-weight: 300;
    }
    
    label.radio input:checked + span {
      border-color: #fff;
      background-color: #2b2b2b;
      color: #fff;
    }
    
    .credit-inputs {
      background: #2b2b2b;
      color: #fff !important;
      border-color: rgb(0, 0, 0);
    }
    
    .credit-inputs::placeholder {
      color: #fff;
      font-size: 13px;
    }
    
    .credit-card-label {
      font-size: 9px;
      font-weight: 300;
    }
    
    .form-control.credit-inputs:focus {
      background: rgb(102,102,221);
      border: rgb(102,102,221);
    }
    
    .line {
      border-bottom: 1px solid rgb(102,102,221);
    }
    
    .information span {
      font-size: 12px;
      font-weight: 500;
    }
    
    .information {
      margin-bottom: 5px;
    }
    
    .items {
      -webkit-box-shadow: 5px 5px 4px -1px rgba(0,0,0,0.25);
      box-shadow: 5px 5px 4px -1px rgba(0, 0, 0, 0.08);
    }
    
    .spec {
      font-size: 11px;
    }
    .clr_dark{
      background-color: #141414;
    }
    .clr_white{
      color: white;
    }
    .bg_ccllr{
      background-color: #2b2b2b;
      color: white;
    }
    /* slider */
    #main_slide{
      position: relative;
 width: 100%;
 height: 300px;
 background-color: #2b2b2b;
 border-radius: 5px;
    }
    #center_slider{
position: absolute;
left: 25%;
top: 50%;
transform: translate(0%,-50%);
      width: 70%;
      height: 80%;
      
    }
    .imagecontainer_slider{
      height: 100%;
      overflow: hidden;
      width: 0%;
      position: absolute;
    }
    .img_slider{
      aspect-ratio: 9/4;
      height: 100%;
      object-fit: cover;
    }
    .text_slider{
      position: absolute;
      bottom: 49%;
      left: -25%;
      overflow: hidden;
      width: 400px;
      height: 12vw;
      z-index: 9999;
    }
    .p_slider{
      position: absolute;
bottom: -2%;
left: -25%;
overflow: hidden;
width: 371px;
height: 12vw;
z-index: 9999;
    }
    .text_slider h1{
      font-size: 6vw;
      position: absolute;
      top: 100px;
      color: white;
      font-family: 'Noto Serif Display', serif;
    }
    .p_slider p{
      width: 100%;
      position: absolute;
top: 150px;
color: white;

    }
    @media (max-width: 767px) {
      .p_slider{
        width: 68%;
        height: 13vw;
        bottom: 19%;
        left: -27%;
}
.p_slider p{
  font-size: 2.5vw;
}
      .img_slider{
        aspect-ratio: 5.5/4;
      }
      #main_slide{
        height: 80vw;
      }
      .text_slider {
       
        width: 100%;
        height: 13vw;
      }
    
      .text_slider h1 {
        font-size: 13vw;
        top: 100px;
      }
    }
    
    /* Media query for medium screens */
    @media (min-width: 768px) and (max-width: 1023px) {
      .p_slider{
        left: -30%;
        bottom: 8%;
      }
      .p_slider p{
        font-size: 1.5vw;
      }
      #main_slide{
        height: 400px;
      }
      .img_slider{
        aspect-ratio: 10/4;
      }
      .text_slider {
        left: -14%;
        width: 100%;
        height: 7vw;
      }
    
      .text_slider h1 {
        font-size: 7vw;
        top: 50px;
      }
    }
    
    /* Media query for large screens */
    @media (min-width: 1024px) {
      .p_slider{
        left: -28%;
        bottom: 8%;
      }
      .img_slider{
        aspect-ratio: 9/4;
      }
      #main_slide{
        height: 500px;
      }
      .text_slider {
        width: 100%;
        height: 10vw;
      }
    
      .text_slider h1 {
        font-size: 10vw;
        top: 100px;
      }
    }
